<template>
  <div class="hello">
    <ul>
      <li v-for="(item,index) in list" :key="item.id">
        {{++index}}-----{{item.id}}-----{{item.name}}
      </li>
    </ul>
    <input type="button" value="点击" @click="btnclick"/>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'helloword ',
      list: []
    }
  },
  methods: {
    loadData () {
      axios.get('https://localhost:5001/api/First/GetData').then((result) => {
        console.log(result.data)
        this.list = result.data
      }).catch((err) => {
        console.log(err)
      })
    },
    btnclick: function () {

    }
  },
  mounted () {
    this.loadData()
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

</style>



